import { Component, OnInit } from '@angular/core';
import {IonSlides, PopoverController } from '@ionic/angular';
import { MenuPage } from '../menu/menu.page';
import { HttpClient, HttpClientModule, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-brandlevel2',
  templateUrl: './brandlevel2.page.html',
  styleUrls: ['./brandlevel2.page.scss'],
})
export class Brandlevel2Page implements OnInit {
  slideOptions = {
    initialSlide: 1,
    speed: 2000,
  };
  listProduct:any| undefined;
  constructor(private popoverController:PopoverController,private http: HttpClient) { }
  slidesDidLoad(slides: IonSlides) {
    slides.startAutoplay();
  }
  ngOnInit() {
    this.getCategory();
  }


  async getCategory() {
    await this.http.get(environment.location + '?route=api/product')
      .subscribe(Response => {
        var data = JSON.parse(JSON.stringify(Response));
        this.listProduct = data.products;
        console.log(this.listProduct);
      },
        error => {
          const info = JSON.parse(JSON.stringify(error));
          // this.toastr.error(error.status === 0 ? '500 Internal Server Error' : (info.error.message != '' ? info.error.error : info.error.message));
        }
      )
  }

  async menuOpen()
  {
    const popover = await this.popoverController.create({
      component: MenuPage,
      cssClass: 'menuList',
    });
    await popover.present();
  }

}
